.profile-img {
    width: 160px;
    height: 160px;
    border-radius: 50%;
}

.profile-main {
    display: flex;
    justify-content: space-around;
    margin: 10px auto;
    border-bottom: 1px solid rgba(0,0,0,.2);
    padding-bottom: 2rem;
    padding-top: 2rem;
}

.info-profile {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
}

.profile .gallery {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 1rem;
    margin-bottom: 30px;
}

.profile .gallery img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.home__card:not(1) {
    margin: 30px auto;
}

.home__card h4 {
    padding: 10px 15px;
    font-weight: bold;
    font-size: 16px;
    margin: 0;
}

.card__content {
    padding: 20px!important;
}

.card__content h6 {
    font-size: 20px;
    font-weight: bold;
    color: #000;
    font-weight: lighter;
}

.card__content p {
    font-size: 12px;
    color: grey;
}

.create-post {
    padding: 2rem;
}
.cardPost img   {
    height: 350px;
    object-fit: cover;
}


.home-row {
    width: 100%;
    display: grid;
    grid-template-columns: 70% 30%;
    gap: 1.5rem;
    /* margin: 40px 0; */
}


.home-row .home-posts { 
    min-height: 90vh;
    padding: 40px 10%;
}

.home-row .home-right {
    padding: 40px 0;
}

.card.horizontal {
    width: 100%!important;
}

.card__stacked {
    display: block;
    flex-direction: column;
    gap: 10px;
    width: 100%;
}

.card__stacked .card__body{
    margin: 0!important;
    padding: 15px 10px 0 15px!important;
    border-top: 1px solid  rgba(0,0,0,.2);
    width: 100%!important;
}
.card__stacked .card__body p {
    margin-bottom: 20px!important;
    margin-top: 0!important;
    font-size: 13px;
    color: grey;

}

.card__stacked .card__text{
    padding: 15px;
}

.heading {
    width: 100%;
    color: #2135f9;
    padding: 5px 7px;
    background-color: rgba(0,0,0,.05);
    margin-bottom: 5px;
    border-radius: 8px;
    display: block;
}
 
@media screen and (max-width: 728px) {
    .home-row {
        grid-template-columns: 100%;
    }

    .home-posts {
        padding: 20px 0!important;
    }

    .home-right {
        display: none;
    }
}

.like_row{
    display: flex;
    align-items: center;
    gap: 5px;
}

.like-btn{
    cursor: pointer;
    user-select: none;
    background-color: transparent!important;
    border:none!important;
    outline: none!important;
    padding: 0!important;
}

.like-btn:disabled{
    opacity: 0.5;
}


