.avatar-pic {
    position: relative!important;
    width: 200px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.avatar-pic img {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}

.avatar-pic label {
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: .3s ease;
}

.avatar-pic label input {
    width: 0;
    opacity: 0;
    pointer-events: none;
}

.avatar-pic:hover label {
    opacity: 1;
}


.modal-container {
    position: fixed;
    height: 100vh;
    width: 100%;
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,.4);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999999999;
    color: #000!important;
}

.modal-container .modals {
    width: 350px;
    height: 450px;
    border-radius: 8px;
    background-color: #fff;
    padding: 2rem;
    overflow: auto;
}

.flex{
    display: flex;
    gap: 10px;
    align-items: center;
}
.modals .close {
    display: flex;
    justify-content: flex-end;
    cursor: pointer;
}